<template>
  <div>
    <div id="map"></div>
    <template>
      <div id="markup-type-checkbox">
        <el-radio-group v-model="markupType">
          <el-radio label="vector" value="vector"></el-radio>
          <el-radio label="overlay" value="overlay"></el-radio>
          <el-radio label="none" value="none"></el-radio>
        </el-radio-group>
      </div>
      
      <div id="label" style="display:none;">
        <div class="marker" v-for="i in markerNo" :key="i" :id="'marker' + i" title="Marker">
          <a class="address" :id="'address' + i" target="_blank" href="http://rsgis.whu.edu.cn">标注点</a>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import {Map, View} from 'ol';
import TileLayer from 'ol/layer/Tile';
import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import OSM from 'ol/source/OSM';
import $ from 'jquery';

import {Control} from 'ol/control';
import {toStringXY} from 'ol/coordinate';

import {fromLonLat} from 'ol/proj';
import Feature from 'ol/Feature';
import {Point} from 'ol/geom';
import Overlay from 'ol/Overlay';
import {Style, Icon, Text, Fill, Stroke} from 'ol/style';

export default {
  name: 'simple-markup',
  watch: {
    markupType: (val, oldVal) => {
      console.log(val);
    },
  },
  data() {
    return {
      map: {},
      view: {},
      layers: [],
      markupType: 'vector',

      // Position
      beijing: [116.394653, 39.911957],
      wuhan: [114.299011, 30.592976],

      // VectorFeature
      iconFeature: {},

      // VectorLayer
      VectorSource: {},
      VectorLayer: {},

      // Overlay Marker
      marker: {},
      text: {},

      markerNo: 1,
    }
  }, // data
  methods: {
    preDefine() {
      // view
      this.view = new View({
        center: [114.309654, 30.556240],
        zoom: 5,
        projection: 'EPSG:4326'
      });
      // layers
      this.layers = [
        new TileLayer({
          source: new OSM()
        }),
      ]
    },
    createMap() {
      this.map = new Map({
        target: 'map',
        view: this.view,
        layers: this.layers
      });
      // 添加自定义控件
      this.map.addControl(
        new Control({
          element: $('#markup-type-checkbox')[0]
        })
      );
      // 监听鼠标点击事件
      this.map.on('click', e => {
        // 显示坐标
        let coordinate = toStringXY(e.coordinate, 6);
        let zoom = e.map.getView().getZoom();
        this.$notify({
          message: coordinate + ', ' + zoom,
          type: 'success'
        });
        console.log(coordinate);

        // 添加标记
        if (this.markupType === 'overlay') this.addOverlayLabel(e.coordinate);
        else if (this.markupType === 'vector') this.addVectorLabel(e.coordinate);
      });
    },
    addIconAndLayer() {
      // 1. Vector Label
      this.iconFeature = new Feature({
        geometry: new Point(this.beijing),
        name: '北京市',         //名称
        population: 2115,      //人口
      });
      this.iconFeature.setStyle(this.createLabelStyle(this.iconFeature));
      this.VectorSource = new VectorSource({
        features: [this.iconFeature]
      });
      this.VectorLayer = new VectorLayer({
        source: this.VectorSource
      });
      this.map.addLayer(this.VectorLayer);
      
      // 2. Overlay Label
      let labelElement = $('#marker1')[0];
      let textElement = $('#address1')[0];
      textElement.innerText = '武汉市';
      
      this.marker = new Overlay({
        position: this.wuhan,
        positioning: 'center-center',
        element: labelElement,
        stopEvent: false
      });
      this.text = new Overlay({
        position: this.wuhan,
        element: textElement
      });

      this.map.addOverlay(this.text);
      this.map.addOverlay(this.marker);
    },
    createLabelStyle(feature) {
      return new Style({
        image: new Icon({
          anchor: [0.5, 60],
          anchorOrigin: 'top-right',
          anchorXUnits: 'fraction',
          anchorYUnits: 'pixels',
          offsetOrigin: 'top-right',
          opacity: 0.75,
          src: '/image/blueIcon.png'
        }),
        text: new Text({
          textAlign: 'center',
          textBaseline: 'middle',
          font: 'normal 14px 微软雅黑',
          text: feature.get('name'),
          // 文字颜色
          fill: new Fill({color: '#a30'}),
          stroke: new Stroke({color: '#fc3', width: 2})
        })
      });
    },
    addVectorLabel(coordinate) {
      let newFeature = new Feature({
        geometry: new Point(coordinate),
        name: '标注点'
      });
      newFeature.setStyle(this.createLabelStyle(newFeature));
      this.VectorSource.addFeature(newFeature);
    },
    addOverlayLabel(coordinate) {
      const n = ++this.markerNo;
      setTimeout(_ => {
        let elementDiv = $('#marker' + n)[0];
        let elementA = $('#address' + n)[0];

        let newMarker = new Overlay({
          position: coordinate,
          positioning: 'center-center',
          element: elementDiv,
          stopEvent: false
        });

        let newText = new Overlay({
          position: coordinate,
          element: elementA
        });

        this.map.addOverlay(newMarker);
        this.map.addOverlay(newText);

        // 主动渲染地图，更新Overlay元素的相对位置
        this.map.render();
      }, 0);
    }
  }, // methods
  mounted() {
    // 获取屏高方法
    let h = $('.content-box').css('height');
    h = (h.split('px')[0]).toString() + 'px';
    $('#map').css('height', h);

    this.preDefine();
    this.createMap();
    this.addIconAndLayer();
    
    this.addOverlayLabel();
  }, // mounted
}
</script>

<style scoped>
#map {
  height: 100%;
  width: 100%;
}
#markup-type-checkbox {
  position: absolute;
  top: 0.5em;
  left: 3.0em;
}
.marker {
  width: 20px;
  height: 20px;
  border: 1px solid #088;
  border-radius: 10px;
  background-color: #0ff;
  opacity: 0.5;
}
.address {
  text-decoration: none;
  color: #a30;
  font-size: 14px;
  font-weight: bold;
  text-shadow: black 0.1em 0.1em 0.2em;
}
</style>